.select-stroke{
	position: relative;
	width: 100%;
	height: 30vh;
	bottom: 30vh;
	background: #fff;
	text-align: center;
}

.stroke-wrapper{
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}



