.upload-overlay{
	position: absolute;
	width: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    background: #8EE4AF;
}

.upload-btn-wrapper{
	position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 100%;
    height: 200px;
    text-align: center;
}

#btn-file{
	display: none;
}


